<template>
    <div>
        <v-header>
        </v-header>

        <v-content :type="'none'" v-show="loaded">
            <div v-show="isVerify">
                <div class="section mg-t10">
                    <div class="matter" style="height:auto;">
                        <span class="img avatar"
                              :style="userInfo.icon ? 'background-image:url('+ userInfo.icon +')' : ''"></span>
                        <div class="real-info">
                            <p class="font-16">{{userInfo.realName}}
                                <span class="has-real" v-if="userInfo.realStatus == 4">已实名</span>
                            </p>
                            <p class="font-12 color-9">{{userInfo.identifyNumber}}</p>
                            <span class="at-real color-main" v-if="userInfo.realStatus == 1"
                                  v-link="{name:'real-step2',query:{type:type}}">上传头像 > </span>
                            <span class="at-real color-link" v-if="userInfo.realStatus == 2">等待审核</span>
                            <span class="at-real" v-if="userInfo.realStatus == 3" style="color:#fa3c46;"
                                  @click="verifyMsg">
                                <span class="img help03"></span>
                                <span style="margin-left: .01rem;">认证失败</span>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="!isVerify">
                <div class="section">
                    <div class="matter">
                        <p class="title">真实姓名</p>
                        <div class="edit">
                            <p v-show="userInfo.realName">{{userInfo.realName}}</p>
                            <input type="text" placeholder="请输入真实姓名（必填）"
                                   class="full card-lis realName" v-model="realName" maxlength="4"
                                   v-show="!userInfo.realName"/>
                        </div>
                    </div>
                    <div class="matter">
                        <p class="title">身份证号</p>
                        <div class="edit">
                            <p v-show="userInfo.identifyNumber">{{userInfo.identifyNumber}}</p>
                            <input type="text" placeholder="请输入身份证号码（必填）" class="full card-lis identifyNumber"
                                   v-model="identifyNumber" maxlength="18" v-show="!userInfo.identifyNumber"/>
                        </div>
                    </div>
                </div>

                <v-inner-btn style="margin-top:.3rem">
                    <input :class="['input',isEdit || userInfo.realName ? 'ok' : 'nk']" value="下一步" type="button"
                           @click="next"/>
                </v-inner-btn>

                <div class="mg-t10" style="padding:0 .2rem;">
                    <p class="identify-tip">
                        <span class="img tip"></span>
                        认证说明：
                    </p>
                    <p style="text-align:left;" class="font-12 color-9">
                        请使用您本人身份证进行认证，进行支付交易时仅能通过您本人
                        名下的银行卡进行交易。
                    </p>
                </div>
            </div>
        </v-content>
    </div>
</template>
<script>
    import {url, lt} from '../../../const'
    import {imgHost} from '../../../filter'
    import {toast} from '../../../assets/js/toast'
    import VHeader from '../../../components/Header'
    import VContent from '../../../components/Content'
    import VInnerBtn from '../../../components/InnerBtn'
    import $ from 'jquery'

    export default{
        route: {
            data({from, to}){
                this.realName = '';
                this.identifyNumber = '';
                this.isEdit = false;
                this.type = to.query.type;

                this.loaded = false;
                return this.$http.get(url.autonymVerify).then(
                    ({data:{data}}) => {
                        this.userInfo = data.userInfo;
                        let icon = data.userInfo.icon;
                        if (icon) {
                            this.userInfo.icon = imgHost(icon);
                        }
                        this.isVerify = data.isVerify;
                        this.remark = data.userInfo.remark;
                        this.loaded = true;
                    })
            }
        },
        ready(){
            let vm = this;
            let bindLis = vm.$watch('loaded', function () {
                if (this.loaded && !this.isVerify) {//加载完并且未认证时触发
                    vm.$watch('realName',function () {
                        vm.isEdit = vm.checkEdit();
                    });
                    vm.$watch('identifyNumber',function () {
                        vm.isEdit = vm.checkEdit();
                    });
                    $('.card-lis').blur(function () {
                        if ($(this).is('.realName')) {
                            if (vm.realName.length < 2) {
                                toast.alert('姓名最少为两个字');
                            }
                        } else {
                            let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                            if (!reg.test(vm.identifyNumber)) {
                                toast.alert('身份证格式不正确');
                            }
                        }
                    });
                    bindLis();
                }
            });
        },
        data(){
            return {
                isVerify: false,
                userInfo: {},//icon:'',realName:'',identifyNumber:''
                realName: '',
                identifyNumber: '',
                isEdit: false,
                loaded: false,
                type: 0,
                remark: ''
            }
        },
        methods: {
            verifyMsg(){
                let vm = this;
                let remark = this.remark ? this.remark : '您上传的证件图片不符合平台审核要求，请重新上传！';
                toast.conform(remark, function () {
                    window.location.href = `#/identify/real/step2?type=${vm.type}`;
                }, false, null, {do: '修改'});
            },
            checkEdit(){
                let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                return this.realName.length > 1 && reg.test(this.identifyNumber);
            },
            next(){
                if (this.isEdit) {
                    this.$http.get(url.verifyIdCard, {
                        realName: this.realName,
                        identifyNumber: this.identifyNumber
                    }).then(
                        ({data:{data, success, resultMsg}}) => {
                            if (success) {
                                window.location.href = `#/identify/real/step2?type=${this.type}`;
                            } else {
                                toast.alert(resultMsg);
                            }
                        })
                    return;
                }
                if (this.userInfo.realName) {
                    window.location.href = `#/identify/real/step2?type=${this.type}`;
                }
            }
        },
        components: {VHeader, VContent, VInnerBtn}
    }


</script>
<style scoped>
    .identify-tip {
        text-indent: .17rem;
        position: relative;
        text-align: left;
        font-size: .12rem;
        color: #999;
    }

    .img.tip {
        top: .01rem;
        left: 0;
        width: .14rem;
        height: .14rem;
        background-size: .14rem;
        position: absolute;
        background-image: url(../../../assets/img/icon/tip01.png);
    }

    .img.avatar {
        width: .5rem;
        height: .5rem;
        position: absolute;
        top: .22rem;
        left: 0;
        border-radius: 50%;
        background-size: .5rem;
        background-image: url(../../../assets/img/user/photo02.jpg);
    }

    .real-info {
        margin-left: .6rem;
        text-align: left;
        line-height: 2;
        padding: .2rem 0;
        position: relative;
    }

    .has-real {
        background-color: #4fbcf4;
        border-radius: .02rem;
        color: #fff;
        font-size: .12rem;
        padding: .02rem;
    }

    .at-real {
        position: absolute;
        right: .1rem;
        top: .25rem;
        font-size: .12rem;
    }

    .img.help03 {
        width: .15rem;
        height: .15rem;
        background-size: .15rem;
        background-image: url(../../../assets/img/icon/help03.png);
        display: inline-block;
        vertical-align: text-bottom;
    }
</style>




